import React, { useState, useEffect, useRef, useCallback } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { useHistory } from "react-router-dom";
import Grid from "@material-ui/core/Grid";
import Card from "@material-ui/core/Card";
import Button from "@material-ui/core/Button";
import SpeedProgress from "../feedback/SpeedProgress";
import { StyledTextField } from "../common";
import { Typography } from "@material-ui/core";
import styled, { keyframes } from "styled-components";
import Follower from "./components/Follower";
import WordBox from "./components/WordBox";
import "./styles.css";

const passages = [
  "Specialized uniforms, such as nun and priest garb, can be most helpful. Check out your local uniform store for a wide range of clothes that will get you in, and especially out, of all kinds of stores.",
  "It's enough for me to be sure that you and I exist at this moment.",
  "We've known each other for many years but this is the first time you've ever come to me for counsel or for help. I can't remember the last time you invited me to your house for a cup of coffee, even though my wife is Godmother to your only child. But, let's be frank here. You never wanted my friendship and you were afraid to be in my debt.",
  "The moon belongs to everyone; the best things in life are free. The stars belong to everyone; they gleam there for you and me. The flowers in spring, the robins that sing, the sunbeams that shine - they're yours, they're mine. And love can come to everyone - the best things in life are free!",
  "When the shoes first fell from the sky, he remembered thinking that destiny had struck him. Now he thought so again. It was more than a coincidence. It had to be destiny.",
  "Higher and higher he climbed. His strength came from somewhere deep inside himself and also seemed to come from the outside as well. After focusing on Big Thumb for so long, it was as if the rock had absorbed his energy and now acted like a kind of giant magnet pulling him toward it.",
  "We were playing checkers. I used to kid her once in a while because she wouldn't take her kings out of the back row. But I didn't kid her much though. You never wanted to kid Jane too much. I think I really like it best when you can kid the pants off a girl when the opportunity arises, but it's a funny thing. The girls I like best are ones I never feel much like kidding.",
  "In every war, there are calms between storms. There will be days when you lose faith in some of us, days when our allies turn against us, but the day will never come if we forsake this planet and its people.",
  "Say what you will. But a person just cannot know what he doesn't know. And you can't always see that a bad thing is going to happen before it happens. If you could, no bad would ever come.",
  "There are very few things I wish I could change about those two decades, but there are many things I wish I had known. I wish I had known, for instance, that such a long period of being single was in the cards for me. I also wish I'd known earlier that such a delay was not abnormal among my generation.",
  "I'd ask you about love, you'd probably quote me a sonnet. But you've never looked at a woman and been totally vulnerable. Known someone that could level you with her eyes, feeling like God put an angel on earth just for you.",
];

const getWidth = () =>
  window.innerWidth ||
  document.documentElement.clientWidth ||
  document.body.clientWidth;

export default function StandardGame(props) {
  // Race Info
  const [startTime, setStartTime] = useState(0); // The time the current race started
  const [numWpmIntervals, setNumWpmIntervals] = useState(0); // The number of intervals to show in wpm graph result
  const [textAreaText, setTextAreaText] = useState("");
  const [words, setWords] = useState([]); // The array of words the passage used

  // Race State
  const [isRaceRunning, setIsRaceRunning] = useState(false);
  const [isCorrect, setIsCorrect] = useState(true); // If the user currently has typed an error in the passage

  // Tracking
  const [currentCharIndex, setCurrentCharIndex] = useState(0); // The current character the user is on in the passage (Regardless if they are correct or not)
  const [currentWordIndex, setCurrentWordIndex] = useState(0); // The index of the start of the word the user is currently on
  const [wordIndex, setWordIndex] = useState(0); // The index of the start of the word the user is currently on (Regardless if they are correct or not)
  const [errorIndex, setErrorIndex] = useState(0); // The index where the user made an error and must go back to
  const [wordsTyped, setWordsTyped] = useState(0);
  //const [missedWords, setMissedWords] = useState({});
  const [wpmIntervals, setWpmIntervals] = useState([]); // array of wpm's between each isolated interval
  const [currentWpmInterval, setCurrentWpmInterval] = useState(0);
  const [currentIntervalStart, setCurrentIntervalStart] = useState(0);
  const [currentMissedChars, setCurrentMissedChars] = useState(0);
  const [currentMissedWords, setCurrentMissedWords] = useState(0);

  // Element States
  const inputRef = useRef(null);
  const wbRef = useRef(null);
  const [currentCharOffsetLeft, setCurrentCharOffsetLeft] = useState(0);
  const [currentCharOffsetTop, setCurrentCharOffsetTop] = useState(0);
  const [currentCharWidth, setCurrentCharWidth] = useState(20);
  const [inputDisabled, setInputDisabled] = useState(false);
  const [wpm, setWPM] = useState(0);
  const [wpmColor, setWpmColor] = useState("red");
  const [showResults, setShowResults] = useState("none");

  const [wpmUpdate, setWpmUpdate] = useState(0);

  const history = useHistory();

  const useStyles = makeStyles(
    (theme) => ({
      Border: {
        padding: "4px",
        background: theme.palette.background.paper,
        borderRadius: "3px",
        borderLeft: "2px solid white",
      },
      Card: {
        padding: theme.spacing(2),
        textAlign: "left",
        color: theme.palette.text.secondary,
        borderRadius: "3px",
      },
      textArea: {
        position: "relative",
        padding: "25px",
        margin: "30px",
        fontSize: "25pt",
        minHeight: "250px",
        lineHeight: "normal",
        textAlign: "left",
      },
      button: {
        display: "inline-block",
      },
      type: {
        padding: "10px",
      },
      resultsSection: {
        marginTop: "50px",
        display: showResults,
      },
      result: {
        width: "100%",
        textAlign: "center",
        borderBottom: `1px solid ${theme.palette.text.primary}`,
        lineHeight: "0.1em",
        margin: "10px 0 20px",
      },
      resultSpan: {
        background: theme.palette.background.default,
        padding: "0 10px",
      },
      word: {
        display: "inline-block",
        padding: "0 0",
      },
      letter: {
        display: "inline-block",
      },
      correct: {
        color: "white",
      },
      incorrect: {
        color: theme.palette.textColor.incorrect,
      },
    }),
    { name: "MuiStandardGame" }
  );

  let classes = useStyles();

  const OnStartRace = () => {
    setInputDisabled(false);
    inputRef.current.focus();

    setStartTime(Date.now());

    setNumWpmIntervals(Math.round(words.length / 7));
    setWpmIntervals(new Array(7).fill(0));
    setCurrentIntervalStart(Date.now());

    setIsRaceRunning(true);
    setWpmUpdate(false);
  };

  const updateWPM = () => {
    if (!isRaceRunning) {
      setWPM(0);
      return;
    }
    let currentWpm = (
      (currentCharIndex / 5 / (Date.now() - startTime)) *
      60000
    ).toFixed(1);
    setWPM(currentWpm);
    let red = currentWpm * 3;
    let green = currentWpm > 100 ? 255 - (currentWpm - 100) ** 1.203 : 255;
    setWpmColor(`rgb(${red},${green},0)`);
    setWpmUpdate((previous) => previous + 1);
  };

  const OnEndRace = () => {
    setIsRaceRunning(false);

    updateWPM();

    let numCharactersTyped = 0;
    for (
      let index = wordsTyped - (words.length % 7);
      index < wordsTyped;
      index++
    ) {
      numCharactersTyped += words[index].length;
    }
    //wpmIntervals[currentWpmInterval] = parseFloat(((((numCharactersTyped + (words.length % 7) - 1) / 5) / Date.now() - currentIntervalStart)) * 60000).toFixed(1);
    setShowResults("block");

    let nextPassageIndex = Math.floor(Math.random() * passages.length);
    setTextAreaText(passages[nextPassageIndex]);
    setWords(passages[nextPassageIndex].split(" "));

    Reset();
  };

  const Reset = () => {
    setCurrentCharIndex(0);
    setIsCorrect(true);
    setCurrentWordIndex(0);
    setWordIndex(0);
    setErrorIndex(0);
    setWordsTyped(0);
    setStartTime(0);
    setCurrentWpmInterval(0);
    setCurrentMissedChars(0);
    setCurrentMissedWords(0);
    setWpmUpdate(0);
    setWPM(0);
    inputRef.current.value = "";
  };

  const CharStyle = {
    None: "None",
    Correct: "Correct",
    Incorrect: "Incorrect",
  };

  const setCharStyle = (charStyle, word, index) => {
    if (!(wbRef.current && wbRef.current.children[word])) return;
    let charDiv = wbRef.current.children[word].children[index];
    switch (charStyle) {
      case CharStyle.None:
        charDiv.className = classes.letter;
        break;
      case CharStyle.Correct:
        charDiv.classList.add(classes.correct);
        break;
      case CharStyle.Incorrect:
        charDiv.classList.add(classes.incorrect);
        break;
    }
  };

  const OnKeyUp = (event) => {
    let key = event.key;

    // If we have reached the end of the passage and we are correct, end the race
    if (
      event.target.value === words[wordsTyped] &&
      isCorrect &&
      currentCharIndex >= textAreaText.length - 1
    ) {
      OnEndRace();
    }
  };

  const OnKeyDown = (event) => {
    let key = event.key;

    if (!isRaceRunning) {
      OnStartRace();
    }

    // This is neccessary because the states are not updated before the rest of the code is executed, so this is used to hold the new values (mainly for styling purposes)
    let tempCurrentCharIndex = currentCharIndex;
    let tempWordIndex = wordIndex;
    let tempWordsTyped = wordsTyped;
    let newCharStyle = CharStyle.None;
    if (key === "Backspace") {
      // We shouldn't be able to go back past the current word we are on
      if (currentCharIndex > currentWordIndex) {
        setCurrentCharIndex((prev) => prev - 1);
        tempCurrentCharIndex--;

        // If we go back past where an error was made, we have corrected it
        if (currentCharIndex - 1 <= errorIndex) {
          setIsCorrect(true);
        }

        // Going back to a new word
        if (textAreaText.charAt(currentCharIndex - 1) === " ") {
          setWordsTyped((prev) => prev - 1);
          tempWordsTyped--;
          setWordIndex(currentCharIndex - words[wordsTyped - 1].length - 1);
          tempWordIndex = currentCharIndex - words[wordsTyped - 1].length - 1;
        }
        setCharStyle(
          newCharStyle,
          tempWordsTyped,
          tempCurrentCharIndex - tempWordIndex
        );
      }
    }

    // Keys like alt and ctrl should be ignored for now
    if (key.length !== 1) return;

    if (isCorrect) {
      // We have misstyped a character
      if (key !== textAreaText.charAt(currentCharIndex)) {
        setErrorIndex(currentCharIndex);
        setIsCorrect(false);
        newCharStyle = CharStyle.Incorrect;

        // Increment the word trackers even if we are wrong on a space
        if (textAreaText.charAt(currentCharIndex) === " ") {
          setWordIndex(currentCharIndex + 1);
          tempWordIndex++;
          setWordsTyped((prev) => prev + 1);
          tempWordsTyped++;
        }
      }
      // We have successfully typed a word correctly
      else if (words[wordsTyped].startsWith(event.target.value)) {
        if (key === " ") {
          setCurrentWordIndex(currentCharIndex + 1);
          tempWordIndex++;
          setWordIndex(currentCharIndex + 1);
          setWordsTyped((prev) => prev + 1);
          tempWordsTyped++;
          inputRef.current.value = "";
          event.preventDefault();
        }

        newCharStyle = CharStyle.Correct;
      }
    }
    // If we are not correct
    else {
      // Increment the word trackers even if we are wrong on a space
      if (textAreaText.charAt(currentCharIndex) === " ") {
        setWordIndex(currentCharIndex + 1);
        tempWordIndex++;
        setWordsTyped((prev) => prev + 1);
        tempWordsTyped++;

        newCharStyle = CharStyle.Incorrect;
      }
    }

    // Always increment currentCharIndex if we have typed a letter unless we are at the end of the passage
    if (currentCharIndex < textAreaText.length - 1) {
      setCurrentCharIndex((prev) => prev + 1);
      tempCurrentCharIndex++;
    }

    setCharStyle(newCharStyle, wordsTyped, currentCharIndex - wordIndex);
  };

  const updateFollower = () => {
    if (wbRef.current && wbRef.current.children[1]) {
      let charInfo =
        wbRef.current.children[wordsTyped].children[
          currentCharIndex - wordIndex
        ];
      setCurrentCharOffsetLeft(
        wbRef.current.offsetLeft + charInfo.offsetLeft - 1
      );
      setCurrentCharOffsetTop(wbRef.current.offsetTop + charInfo.offsetTop);
      setCurrentCharWidth(charInfo.offsetWidth + 3);
    }
  };

  useEffect(() => {
    let nextPassageIndex = Math.floor(Math.random() * passages.length);
    setTextAreaText(passages[nextPassageIndex]);
    setWords(passages[nextPassageIndex].split(" "));

    let initFollowerInterval = setInterval(() => {
      if (wbRef.current.children[0]) {
        updateFollower();
        clearInterval(initFollowerInterval);
      }
    }, 100);

    const resizeListener = () => {
      updateFollower();
    };
    // set resize listener
    window.addEventListener("resize", resizeListener);

    // clean up function
    return () => {
      // remove resize listener
      window.removeEventListener("resize", resizeListener);
    };
  }, []);

  useEffect(() => {
    updateFollower();
  }, [currentCharIndex]);

  return (
    <Grid container spacing={3}>
      <Grid item xs={12}>
        <Card className={classes.Border}>
          <Card className={classes.Card}>
            <WordBox words={words} boxRef={wbRef} />
            <Follower
              ccol={currentCharOffsetLeft}
              ccot={currentCharOffsetTop}
              ccw={currentCharWidth}
            />
          </Card>
        </Card>
      </Grid>
      <Grid item xs={12}>
        <Card className={classes.Border}>
          <Card className={classes.Card}>
            <StyledTextField
              className={classes.type}
              required
              fullWidth
              id="type"
              placeholder="Type Here ..."
              name="type"
              fontSize="25px"
              autoComplete="off"
              onKeyDown={(e) => OnKeyDown(e)}
              onKeyUp={(e) => OnKeyUp(e)}
              onFocus={(e) => (e.target.placeholder = "")}
              onBlur={(e) => (e.target.placeholder = "Type Here ...")}
              disabled={inputDisabled}
              inputRef={inputRef}
            />
          </Card>
        </Card>
      </Grid>
      <Grid item xs={12} className={classes.resultsSection}>
        <Typography variant="h3" className={classes.result}>
          <span className={classes.resultSpan}>Results</span>
        </Typography>
      </Grid>
    </Grid>
  );
}
