import React from "react";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import Pages from "./pages";
import PrivateRoute from "./components/PrivateRoute";
import PublicRoute from "./components/PublicRoute";

export default  () => (
    <BrowserRouter>
        <Switch>
            <Route exact path="/" component={Pages.Home}/>
            <PrivateRoute exact path="update-profile" component={Pages.UpdateProfile}/>
            <PublicRoute exact path="/signup" component={Pages.Signup}/>
            <PublicRoute exact path="/login" component={Pages.Login}/>
            <Route exact path="/forgot-password" component={Pages.ForgotPassword}/>
        </Switch>
    </BrowserRouter>
)