import React, { useState, useEffect, useRef, useCallback } from "react";
import { makeStyles } from "@material-ui/core/styles";

export default function Follower(props) {
  const followerRef = useRef(null);
  const [previousWidth, setPreviousWidth] = useState(0);

  //   const [currentCharOffsetLeft, setCurrentCharOffsetLeft] = useState(0);
  //   const [currentCharOffsetTop, setCurrentCharOffsetTop] = useState(0);
  //   const [currentCharWidth, setCurrentCharWidth] = useState(0);

  const useStyles = makeStyles((theme) => ({
    follower: {
      marginTop: "-2.5px",
      position: "absolute",
      left: `${props.ccol}px`,
      top: `${props.ccot}px`,
      width: `${props.ccw}px`,
      minHeight: "40px",
      backgroundColor: "rgba(158, 219, 145, 0.3)",
      animation: `$followerSlide 0.12s`,
    },
    "@keyframes followerSlide": {
      "0%": {
        // transform: `translate(${
        //   followerRef.current !== null
        //     ? -(followerRef.current.offsetWidth - 3)
        //     : 0
        // }px, 0px)`,
        left: `${
          followerRef.current === null
            ? props.ccol
            : followerRef.current.offsetLeft
        }px`,
        width: `${
          followerRef.current === null ? 0 : followerRef.current.offsetWidth
        }px`,
      },
      "100%": {
        left: `${props.ccol}px`,
        // transform: "translate(0, 0)",
      },
    },
  }));

  const classes = useStyles();

  useEffect(() => {
    // if (followerRef.current !== null) {
    //   console.log("------------------------");
    //   console.log(followerRef.current.offsetWidth);
    //   console.log(props.ccol);
    //   console.log("------------------------");
    // }
    // if (followerRef.current !== null) {
    //   followerRef.current.style.left = `${props.ccol - 2}px`;
    //   followerRef.current.style.top = `${props.ccot}px`;
    //   followerRef.current.style.width = `${props.ccw + 3}px`;
    // }
  });

  return <div className={classes.follower} ref={followerRef}></div>;
}
