import React from "react";
import Follower from "./Follower";
import { withStyles } from "@material-ui/core/styles";

const useStyles = (theme) => ({
  textArea: {
    position: "relative",
    display: "inline-block",
    padding: "25px",
    margin: "30px",
    fontSize: "25pt",
    minHeight: "250px",
    lineHeight: "normal",
    textAlign: "left",
  },
  word: {
    display: "inline-block",
    padding: "0 0",
  },
  letter: {
    display: "inline-block",
  },
});

class WordBox extends React.PureComponent {
  render() {
    const { classes, words, boxRef } = this.props;
    return (
      <div className={classes.textArea} ref={boxRef}>
        {words.map((word, index) => {
          console.log(word);
          //char_count++;
          return (
            <div className={classes.word}>
              {word.split("").map((letter, index) => {
                //char_count++;
                return (
                  <div
                    //key={char_count}
                    className={classes.letter}
                    //ref={char_count === currentCharIndex ? measuredRef : null}
                  >
                    {letter}
                  </div>
                );
              })}
              <div
                className={classes.letter}
                //ref={char_count + 1 === currentCharIndex ? measuredRef : null}
              >
                &nbsp;
              </div>
            </div>
          );
        })}
      </div>
    );
  }
}

export default withStyles(useStyles)(WordBox);
