import firebase from '../firebase';

const ref = firebase.firestore().collection("players");

export const getPlayers = () => {
    return new Promise((resolve, reject) => {
        ref.get().then((item) => {
            const items = item.docs.map((doc) => doc.data());
            resolve(items);
        })
        .catch((err) => reject(err));
    });
}

export const insertPlayer = (username) => {
    return new Promise((resolve, reject) => {
        ref.doc().set({username: username}).then(() => {
            resolve(`Successfully inserted new player ${username}`);
        })
        .catch((err) => reject(err));
    });
}

export const deletePlayer = (id) => {
    return new Promise((resolve, reject) => {
        ref.doc(id).delete().then(() => {
            resolve(`Successfully deleted player ${id}`);
        })
        .catch((err) => reject(err));
    });
}

export const updatePlayer = (id, update) => {
    return new Promise((resolve, reject) => {
        ref.doc(id).update(update).then(() => {
            resolve(`Successfully updated player ${id}`);
        })
        .catch((err) => reject(err));
    });
}

